import React, { useState, useEffect } from 'react';
import { Tabs } from 'antd';
import GeneralSettings from './components/GeneralSettings';
import FeatureSettings from './components/FeatureSettings';

const { TabPane } = Tabs;

const MonetizationSettings = () => {
  const [activeKey, setActiveKey] = useState('1');

  useEffect(() => {
    const hash = window.location.hash.slice(1);
    if (hash === 'feature-settings') {
      setActiveKey('2');
    } else if (hash === 'general-settings') {
      setActiveKey('1');
    } else { 
      const savedKey = localStorage.getItem('activeTabKey');
      setActiveKey(savedKey || '1');  
    }
  }, []);

  const handleTabChange = (key) => {
    setActiveKey(key);
    const hash = key === '2' ? 'feature-settings' : 'general-settings';
    window.location.hash = hash;
    localStorage.setItem('activeTabKey', key);
  };

  return (
    <Tabs activeKey={activeKey} onChange={handleTabChange}>
      <TabPane tab="General Settings" key="1">
        <GeneralSettings />
      </TabPane>
      <TabPane tab="Feature Settings" key="2">
        <FeatureSettings />
      </TabPane>
    </Tabs>
  );
};

export default MonetizationSettings;