import React, { useEffect, useState } from "react";
import { Select, Tooltip, Form, Button, message, Skeleton } from "antd";

const FeatureSettings = () => {
  const [featureSettings, setFeatureSettings] = useState({
    wn_product_of_day_selected_product: [],
  });
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const wpBaseURL = window.wpApiSettings.root;

    // Fetch products
    fetch(`${wpBaseURL}wn_potd/v1/products`)
      .then((response) => {
        if (!response.ok) throw new Error("Failed to fetch products");
        return response.json();
      })
      .then((data) => {
        setProducts(data || []);
      })
      .catch((error) => {
        message.error(`Error loading products: ${error.message}`);
        console.error(error);
      });

    // Fetch feature settings
    fetch(`${wpBaseURL}wn_potd/v1/feature-settings`)
      .then((response) => {
        if (!response.ok) throw new Error("Failed to fetch feature settings");
        return response.json();
      })
      .then((data) => {
        setFeatureSettings({
          wn_product_of_day_selected_product: data.wn_product_of_day_selected_product || [],
        });
        setLoading(false);
      })
      .catch((error) => {
        message.error(`Error loading settings: ${error.message}`);
        console.error(error);
        setLoading(false);
      });
  }, []);

  const handleProductChange = (selectedProducts) => {
    setFeatureSettings((prev) => ({
      ...prev,
      wn_product_of_day_selected_product: selectedProducts,
    }));
  };

  const handleSubmit = () => {
    const wpBaseURL = window.wpApiSettings.root;

    fetch(`${wpBaseURL}wn_potd/v1/feature-settings`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "X-WP-Nonce": window.wpApiSettings?.nonce || "",
      },
      body: JSON.stringify({
        wn_product_of_day_selected_product: featureSettings.wn_product_of_day_selected_product,
      }),
    })
      .then((response) => {
        if (!response.ok) throw new Error("Failed to save settings");
        return response.json();
      })
      .then(() => {
        message.success("Settings saved successfully!");
      })
      .catch((error) => {
        message.error(`Error saving settings: ${error.message}`);
        console.error(error);
      });
  };

  return ( 
    <div 
    style={{ 
      padding: "40px",}}>
      <div style={{ flex: 1, maxWidth: "600px", margin: "0" }}>
        <h1 style={{ marginBottom: "20px", fontSize: "24px", fontWeight: "600" }}>
          Feature Settings
        </h1>
        {loading ? (
          <Skeleton active paragraph={{ rows: 4 }} />
        ) : (
          <Form layout="vertical">
            <Form.Item
              label={
                <Tooltip title="Select the products for Product of the Day">
                  Select Products for Product of the Day
                </Tooltip>
              }
            >
              <Select
                mode="multiple"
                value={featureSettings.wn_product_of_day_selected_product}
                onChange={handleProductChange}
                placeholder="Select products"
                options={products.map((product) => ({
                  label: product.name, // Adjust based on your product API response
                  value: product.id,
                }))}
              />
            </Form.Item>

            <Button
              type="primary"
              onClick={handleSubmit}
              style={{
                width: "100%",
                marginTop: "10px",  
              }}
            >
              Save Settings
            </Button>
          </Form>
        )}
      </div> 
      </div> 
  );
};

export default FeatureSettings;