import React, { useEffect, useState } from "react";
import { Switch, Tooltip, Form, Input, Button, message, Skeleton } from "antd";

const GeneralSettings = () => {
  const [settings, setSettings] = useState({
    wn_product_of_day_allow: "",
    wn_product_of_day_description: "",
    wn_product_of_day_title: "",
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const wpBaseURL = window.wpApiSettings?.root;

    // Fetch existing settings
    fetch(`${wpBaseURL}wn_potd/v1/general-settings`)
      .then((response) => {
        if (!response.ok) throw new Error("Failed to fetch settings");
        return response.json();
      })
      .then((data) => {
        setSettings({
          wn_product_of_day_allow: data.wn_product_of_day_allow || " ",
          wn_product_of_day_description: data.wn_product_of_day_description || "",
          wn_product_of_day_title: data.wn_product_of_day_title || "",
        });
        setLoading(false);
      })
      .catch((error) => {
        message.error(`Error loading settings: ${error.message}`);
        console.error(error);
        setLoading(false);
      });
  }, []);

  const handleToggleChange = (checked) => {
    setSettings((prev) => ({
      ...prev,
      wn_product_of_day_allow: checked ? "on" : " ",
    }));
  };

  const handleTitleChange = (e) => {
    setSettings((prev) => ({
      ...prev,
      wn_product_of_day_title: e.target.value,
    }));
  };

  const handleDescriptionChange = (e) => {
    setSettings((prev) => ({
      ...prev,
      wn_product_of_day_description: e.target.value,
    }));
  };

  const handleSubmit = () => {
    const wpBaseURL = window.wpApiSettings?.root;

    fetch(`${wpBaseURL}wn_potd/v1/general-settings`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "X-WP-Nonce": window.wpApiSettings?.nonce || "",
      },
      body: JSON.stringify({
        wn_product_of_day_allow: settings.wn_product_of_day_allow,
        wn_product_of_day_description: settings.wn_product_of_day_description,
        wn_product_of_day_title: settings.wn_product_of_day_title,
      }),
    })
      .then((response) => {
        if (!response.ok) throw new Error("Failed to save settings");
        return response.json();
      })
      .then(() => {
        message.success("Settings saved successfully!");
      })
      .catch((error) => {
        message.error(`Error saving settings: ${error.message}`);
        console.error(error);
      });
  };

  return ( 
    <div 
    style={{ 
      padding: "40px",}}>
      <div style={{ maxWidth: "600px", margin: "0" }}>
        <h1 style={{ marginBottom: "20px", fontSize: "24px", fontWeight: "600" }}>
          General Settings
        </h1>
        {loading ? (
          <Skeleton active paragraph={{ rows: 6 }} />
        ) : (
          <Form layout="vertical">
            <Form.Item
              label={
                <Tooltip title="Enable or disable Product of the Day">
                  Enable Product of the Day
                </Tooltip>
              }
            >
              <Switch
                checked={settings.wn_product_of_day_allow === "on"}
                onChange={handleToggleChange}
              />
            </Form.Item>

            <Form.Item
              label={
                <Tooltip title="Edit the description for Product of the Day">
                  Product of the Day Description
                </Tooltip>
              }
            >
              <Input.TextArea
                value={settings.wn_product_of_day_description}
                onChange={handleDescriptionChange}
                rows={4}
                placeholder="Enter a description for the product of the day"
              />
            </Form.Item>

            <Form.Item
              label={
                <Tooltip title="Edit the title for Product of the Day">
                  Product of the Day Title
                </Tooltip>
              }
            >
              <Input.TextArea
                value={settings.wn_product_of_day_title}
                onChange={handleTitleChange}
                rows={2}
                placeholder="Enter a title for the product of the day"
              />
            </Form.Item>

            <Button
              type="primary"
              onClick={handleSubmit}
              style={{ width: "100%" }}
            >
              Save Settings
            </Button>
          </Form>
        )}
      </div> 
      </div>
  );
};

export default GeneralSettings;